@import '../../variables';

.categories {
    ul {
        display: flex; // items in one line

        li {
            margin-right: 10px;
            background-color: #f9f9f9;
            padding: 13px 30px; // top&bottom left&right
            border-radius: 30px;
            font-weight: bold;
            cursor: pointer;
            transition: background-color $duration ease-in-out;

            &:last-of-type {
                margin-right: 0px;
            }

            &:hover {
                background-color: darken(#f9f9f9, 2%);
            }

            &:active {
                background-color: darken(#f9f9f9, 7%);
            }

            // selected category
            &.active {
                background-color: #282828;
                color: #fff;
            }
        }
    }
}