.emptyCart {
    max-width: 400px;
    text-align: center;
    margin: 60px auto;

    h2 {
        font-size: 36px;
        margin-bottom: 10px;
    }

    p {
        font-size: 20px;
        color: #777777;
        margin-bottom: 20px;
    }

    img {
        margin-bottom: 20px;
    }

    button {
        span {
            font-size: 15px;
            font-weight: bold;
        }
    }

    @media (max-width: 440px) {
        h2 {
            font-size: 33px;
        }

        p {
            font-size: 17px;
        }
    }
}